.auth {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 0;

    .loginCard {
        padding: 0 !important;
        background-color: transparent !important;
        box-shadow: none !important;

        .content {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
        }

        &-headline {
            font-size: 1rem;
            text-align: center;
            padding: 0 0 4rem 0;
        }
    }

    &-button {
        min-width: 20rem !important;
        margin: 0.7rem 0 !important;
    }

    &-or {
        display: flex;
        justify-content: center;
        margin: 2rem 0;
    }

    .quick-social {

        .button {
            margin: 0 0.6rem;
        }
    }
}