.login {
    padding-bottom: 5rem !important;

    &-headline {
        font-size: 3rem;
        text-align: center;
    }

    &-banner {
        background-image: url('../../assets/homescreen-banner.png');
        background-position: right bottom;
        background-repeat: no-repeat;
        background-size: 55%;
        height: 100%;
    }

    &-caption {
        font-weight: 400;
        font-size: 1rem;
        line-height: 1.2;
        color: #4a4a4a;
        text-align: center;
    }
}